<template><section>
<h2 id="radio-dan-xuan-kuang"><a class="header-anchor" href="#radio-dan-xuan-kuang" aria-hidden="true">¶</a> Radio 单选框</h2>
<p>在一组备选项中进行单选。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-radio label="test" v-model="val1">单选框</fm-radio>
<fm-radio label="test2" v-model="val1">单选框</fm-radio>


</div>
                                                <p>基础的单选框用法，<code>v-model</code> 绑定的是 <code>label</code> 属性对应的值。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"test"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val1"</span>&gt;</span>单选框<span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"test2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val1"</span>&gt;</span>单选框<span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">val1</span>: <span class="hljs-string">''</span>,
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h3>
<p>单选框的不可用状态</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-radio disabled label="test" v-model="val2">不可用</fm-radio>
<fm-radio disabled label="test2" v-model="val2">选中不可用</fm-radio>


</div>
                                                <p>设置 <code>disabled</code> 属性即可。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"test"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val2"</span>&gt;</span>不可用<span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"test2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val2"</span>&gt;</span>选中不可用<span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">val2</span>: <span class="hljs-string">'test2'</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="chi-cun"><a class="header-anchor" href="#chi-cun" aria-hidden="true">¶</a> 尺寸</h3>
<p>指定单选框的大小</p>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-radio label="小尺寸" size="small"></fm-radio>
    <fm-radio label="中尺寸" size="medium"></fm-radio>
    <fm-radio label="大尺寸" size="large"></fm-radio>
</div>
                                                <p>可通过 <code>size</code> 属性指定输入框的尺寸，默认大小是 <code>small</code></p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"小尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"中尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"medium"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"大尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="shu-xing"><a class="header-anchor" href="#shu-xing" aria-hidden="true">¶</a> 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定值</td>
<td style="text-align:left">string/number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">输入框尺寸</td>
<td style="text-align:left">string</td>
<td style="text-align:left">large/medium/small</td>
<td style="text-align:left">small</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">name</td>
<td style="text-align:left">原生 name 属性</td>
<td style="text-align:left">string</td>
<td style="text-align:left">—</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">选中状态的值</td>
<td style="text-align:left">string/boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian"><a class="header-anchor" href="#shi-jian" aria-hidden="true">¶</a> 事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">事件名称</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">当绑定值变化时触发的事件</td>
<td style="text-align:left">(value: string | number, event: Event)</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data () {
            return {
                val1: '',
                val2: 'test2',
                val3: '大尺寸'
            }
        }
    }
</script>


